import { breakpoints } from "../_styleguide/_breakpoints";
import * as d3 from "d3";
import { slugify } from "../helpers/_helpers";
var ChartBarStackedHorizontal = /** @class */ (function () {
    function ChartBarStackedHorizontal(config, svg) {
        this.config = config;
        this.svg = svg;
    }
    ChartBarStackedHorizontal.prototype.draw = function (data, stackedData) {
        this.series = this.svg.layers.data.selectAll(".stackedGroup")
            .data(stackedData);
        this.series.exit().remove();
        this.seriesEnter = this.series
            .enter().append("g")
            .attr("class", function (d) {
            return "stackedGroup " + d.key;
        });
        this.barGroup = this.seriesEnter.merge(this.series)
            .selectAll("g")
            .data(function (d) {
            return d;
        });
        this.barGroup.exit().remove();
        this.barGroupEnter = this.barGroup
            .enter()
            .append("g");
        // dit moet beter kunnen!
        d3.selectAll(".bar").remove();
        d3.selectAll(".barLabel").remove();
        this.bar = this.barGroup.merge(this.barGroupEnter)
            .append("rect")
            .attr("class", function (d) {
            return "bar " + slugify(d.data.group);
        });
        this.bar.exit().remove();
        // this.bars = this.svgLayers.data.selectAll(".bar")
        //     .data(data.filter((d) => d.colour));
        //
        // this.bars.exit().remove();
        //
        // this.barsEnter = this.bars
        //     .enter()
        //     .append("rect")
        //     .attr("class", "bar")
        //     .attr("fill", (d) => colours[d.colour][0])
        // ;
        //
        this.barLabel = this.barGroup.merge(this.barGroupEnter)
            .append('text')
            .attr('class', 'barLabel small-label')
            .attr('x', 0)
            .attr('dx', '0px')
            .attr('dy', '-10px')
            .style("text-anchor", "start");
        this.barLabel.exit().remove();
        this.barValue = this.barGroup.merge(this.barGroupEnter)
            .append('text')
            .attr('class', 'barValue small-label')
            .attr('dx', '0px')
            .attr('dy', '-6px')
            .style("text-anchor", "start");
    };
    ChartBarStackedHorizontal.prototype.redraw = function (dimensions, xScale, yScale, no_respondents) {
        var self = this;
        //     console.log(yScale.bandwidth());
        this.bar
            .attr("x", function (d) { return xScale(d[0]); })
            .attr("y", function (d) {
            return yScale(d.data[self.config.yParameter]);
        })
            .attr("height", yScale.bandwidth())
            .transition()
            .duration(500)
            .attr("width", function (d) {
            return xScale(d[1]) - xScale(d[0]);
        });
        ;
        this.barLabel
            .text(function (d, i) {
            var text = d.data[self.config.yParameter];
            return text;
        })
            .attr('transform', function (d) {
            var offset = (window.innerWidth > breakpoints.bax) ? -(yScale.bandwidth() * 1) : 0;
            return 'translate(' + 0 + ',' + (yScale(d.data[self.config.yParameter]) + (yScale.bandwidth() + offset)) + ')';
        });
        //
        this.barValue
            .text(function (d, i) {
            console.log(d);
            var label = (i === 0) ? "Afgehandeld" : "In behandeling";
            var text = label + ": " + (d[1] - d[0]);
            // if (no_respondents) {
            //     let avg = (Math.round((10 * 100) * (d[self.config.xParameter] / no_respondents)) / 10).toString() + '%';
            //     text = text + ' (' + avg + ')';
            // }
            return text;
        })
            .attr('transform', function (d) {
            var offset = (window.innerWidth > breakpoints.bax) ? -(yScale.bandwidth() * .33) : 0;
            return 'translate(' + (xScale(d[0]) + 30) + ',' + (yScale(d.data[self.config.yParameter]) + (yScale.bandwidth() + offset)) + ')';
        });
        //     .attr('fill-opacity', 0)
        //     .transition()
        //     .delay(500)
        //     .duration(500)
        //     .attr('fill-opacity', 1);
        //     // .attr('fill-opacity', 0)
        //     // .transition()
        //     // .delay(500)
        //     // .duration(500)
        //     // .attr('fill-opacity', 1)
    };
    return ChartBarStackedHorizontal;
}());
export { ChartBarStackedHorizontal };
